
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';


@Component({
  selector: 'app-units-edit',
  templateUrl: './units-edit.component.html',
  styleUrls: ['./units-edit.component.scss'],
})
export class UnitsEditComponent implements OnInit {
  properties:any =[];
  amenities:any=[];
  facing:any=["East","West","North","South","South-East","South-West","North-East","North-West"]; 
   unitsForm: FormGroup;
  editDetails;
  constructor(private formBuilder:FormBuilder, private router:Router,
  public util:UtilService,
    public httpService:HttpServiceService, public alertService:AlertService,
    public loaderService:LoaderService) {
    
  }

  ngOnInit() {
    this.unitsForm = this.formBuilder.group({
      _id: ['', [Validators.required]],
              unitDetails: ['', [Validators.required]],
                propertyId: ['', [Validators.required]],
                rent: ['', [Validators.required]],
                area: ['', [Validators.required]],
                facing: ['', [Validators.required]],
                amenities: ['', [Validators.required]],
                inspection: ['', [Validators.required]],
                propertyavailablefrom: ['', [Validators.required]],
                isAvailable: ['', [Validators.required]],
            });

            this.httpService.getApi('property/getAll').subscribe((res: any) => {
              console.log('properties',res['success'])
              this.properties= res.data
                        })

                        this.httpService.getApi('amenities/getAll').subscribe((res: any) => {
                          console.log('amenities',res['success'])
                          this.amenities= res.data
                                    })
   
    this.loaderService.showLoader('Please wait while fetching ..').then(()=>{
    
    this.editDetails = JSON.parse(localStorage.getItem('editData'))
    this.unitsForm.patchValue(this.editDetails);
    this.loaderService.hideLoader();
  })

  }

  submit(data) {
    
    this.loaderService.showLoader('Updating Please wait ..').then(()=>{
      try{
        this.httpService.postApi(data, 'units/updateDetails/').subscribe((res: any) => {
          this.loaderService.hideLoader();
          if (res["success"]) {
            this.alertService.presentAlert('Success','Successfully updated','Okay');
            this.router.navigate(['/units-list'])
          } else {
            this.alertService.presentAlert('Error',res["error"]["message"],'Okay');
          }
        },(err)=>{
          
          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
         });    
      }catch(e){
        this.loaderService.hideLoader();
        this.alertService.presentAlert('Error','Something went wrong please try again','Okay');
      }
    })
    
  }

}


