
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';

@Component({
  selector: 'app-property-add',
  templateUrl: './property-add.component.html',
  styleUrls: ['./property-add.component.scss'],
})
export class PropertyAddComponent implements OnInit {
  propertTypes:any =[];
  leaseTypes:any=[];
  rentalTypes:any=[];
 
    propertyForm: FormGroup;
  loading;
  constructor(private formBuilder:FormBuilder, private router:Router,
  public util:UtilService,
     public httpService:HttpServiceService, public alertService:AlertService,
     public loaderService:LoaderService) { }

  ngOnInit() {
    this.propertyForm = this.formBuilder.group({ 
                name: ['', [Validators.required]],
                address: ['', [Validators.required]],
                propertyType: ['', [Validators.required]],
                leaseType: ['', [Validators.required]],
                description: ['', [Validators.required]],
                rentalTypeId: ['', []],
                securityDeposit: ['', [Validators.required]],
                isAvailable: ['', [Validators.required]],
            });
   


            this.httpService.getApi('propertyType/getAll').subscribe((res: any) => {
              console.log(res)
              this.propertTypes= res.data
                        })
          
                        this.httpService.getApi('leaseType/getAll').subscribe((res: any) => {
                          console.log(res)
                          this.leaseTypes= res.data
                                    })
                                    
                                    this.httpService.getApi('rentalType/getAll').subscribe((res: any) => {
                                      console.log(res)
                                      this.rentalTypes= res.data
                                                })
                        
                        
  }


  submit(data){
    data['createdAt']= new Date().getTime();
    this.loaderService.showLoader('Adding Please wait ..').then(()=>{
      try{
        this.httpService.postApi(data, 'property/create').subscribe((res) => {
          this.loaderService.hideLoader();
          if(res["success"]){
            this.alertService.presentAlert('Success','Successfully added','Okay');
            this.router.navigate(['/property-list'])
          }else{
            this.alertService.presentAlert('Error',res["message"],'Okay');
          }    
        },(err)=>{
          
          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
         });
      }catch(e){
        this.loaderService.hideLoader();
        this.alertService.presentAlert('Error','Something went wrong please try again','Okay');
      }
    })
    
  }
}


