import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-create-tenant-add',
  templateUrl: './create-tenant-add.component.html',
  styleUrls: ['./create-tenant-add.component.scss']
})
export class CreateTenantAddComponent implements OnInit {

  stage = false;
  localData;
  localData1;
  localData2;
  properties: any = [];
  units: any = [];
  tenants: any = [];
 
  tenantForm: FormGroup;
  assignPropertyToTenantForm: FormGroup;
  agreementForm: FormGroup;
  image;
  document;
  loading;
  //for maintain context
  property;
  unit;
  //validation array start
  Vbankaccount=[{name:'bankName',type:'String'},{name:'bankAddress',type:'String'},{name:'accountNumber',type:'String'},{name:'ifscCode',type:'String'},{name:'mmid',type:'String'},{name:'userId',type:'String'},{name:'isAvailable',type:'String'},{name:'updatedAt',type:'String'},{name:'createdAt',type:'String'}];
  Vagreements=[{name:'userId',type:'String'},{name:'propertyId',type:'String'},{name:'userType',type:'String'},{name:'fromDate',type:'String'},{name:'photo',type:'String'},{name:'toDate',type:'String'},{name:'isAvailable',type:'String'},{name:'updatedAt',type:'String'},{name:'createdAt',type:'String'}];
  Vtenant=[{name:'name',type:'String'},{name:'userId',type:'String'},{name:'email',type:'String'},{name:'mobileNumber',type:'Number'},{name:'currentAddress',type:'String'},{name:'documentsUpload',type:'String'},{name:'tenantPhoto',type:'String'},{name:'agreementType',type:'String'},{name:'backgroundVerificationStatus',type:'String'},{name:'isAvailable',type:'String'},{name:'updatedAt',type:'String'},{name:'createdAt',type:'String'}];
  Vassignpropertytotenant=[{name:'tenantId',type:'String'},{name:'propertyId',type:'String'},{name:'unitId',type:'String'},{name:'isAvailable',type:'String'},{name:'updatedAt',type:'String'},{name:'createdAt',type:'String'}];
 //validation array end
  constructor(private formBuilder: FormBuilder, private router: Router,
    public util: UtilService, public userService: UserService,
    public httpService: HttpServiceService, public alertService: AlertService,
    public loaderService: LoaderService) { }

  ngOnInit() {

    //tenant add
    this.tenantForm = this.formBuilder.group({
      userId: [this.userService.getUserId(), [Validators.required]],
      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      mobileNumber: ['', [Validators.required]],
      currentAddress: ['', [Validators.required]],
      documentsUpload: ['', [Validators.required]],
      tenantPhoto: ['', [Validators.required]],
      agreementType: ['', [Validators.required]],
      backgroundVerificationStatus: ['', [Validators.required]],
      isAvailable: ['1', [Validators.required]],
    });

    //assign property to tenant
    this.assignPropertyToTenantForm = this.formBuilder.group({
      tenantId: ['', [Validators.required]],
      propertyId: ['', [Validators.required]],
      unitId: ['', [Validators.required]],
      isAvailable: ['1', [Validators.required]],
    });

    //agreement form
    this.agreementForm = this.formBuilder.group({
      userId: [this.userService.getUserId(), [Validators.required]],
      propertyId: [' ', [Validators.required]],
      userType: ['', [Validators.required]],
      photo: ['', [Validators.required]],
      fromDate: ['', [Validators.required]],
      toDate: ['', [Validators.required]],
      isAvailable: ['1', [Validators.required]],
  });

    this.httpService.getApi('property/getAll').subscribe((res: any) => {
      this.properties = res.data;
      console.log(this.properties)
    })

  }

  //by changing property
  changeProperty() {
    console.log(this.property)
    this.units.forEach(element => {
      element.propertyId = this.getpropertyId()
    });
    this.httpService.postApi({propertyId:this.property}, 'units/getByCondition').subscribe((res: any) => {
      this.units = res.data;
      console.log(this.units)
    })
  }

  getpropertyId() {
    return this.property;
  }

  //tenant add starts
  changeListenerPhoto($event): void {
    this.loaderService.showLoader("Uploading Image..");
    this.readThisPhoto($event.target);
  }

  readThisPhoto(inputValue: any): void {
    var file: File = inputValue.files[0];
    var myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.image = myReader.result;
      // this.categoryAddForm.patchValue({photo:myReader.result});
      this.httpService.postApi({ tenantPhoto: myReader.result }, 'Item/uploadImage').subscribe((res) => {
        this.loaderService.hideLoader()
        if (res) {
          // res.success
          this.tenantForm.patchValue({ tenantPhoto: res.success });
        } else {
          //this.modal.showModal({'success': false, 'message': res.error});
        }
      }, err => {
        this.loaderService.hideLoader()
        //this.modal.showModal({'success': false, 'message': err});
      });
    }
    myReader.readAsDataURL(file);
  }

  changeListenerDocument($event): void {
    this.loaderService.showLoader("Uploading Image..");
    this.readThisDocument($event.target);
  }

  readThisDocument(inputValue: any): void {
    var file: File = inputValue.files[0];
    var myReader: FileReader = new FileReader();

    myReader.onloadend = (e) => {
      this.document = myReader.result;
      // this.categoryAddForm.patchValue({photo:myReader.result});
      this.httpService.postApi({ documentsUpload: myReader.result }, 'Item/uploadImage').subscribe((res) => {
        this.loaderService.hideLoader()
        if (res) {
          // res.success
          this.tenantForm.patchValue({ documentsUpload: res.success });
        } else {
          //this.modal.showModal({'success': false, 'message': res.error});
        }
      }, err => {
        this.loaderService.hideLoader()
        //this.modal.showModal({'success': false, 'message': err});
      });
    }
    myReader.readAsDataURL(file);
  }

  submit(tenantData) {
    let data:any={};
    tenantData['createdAt'] = new Date().getTime()
    this.loaderService.showLoader('Adding Please wait ..').then(() => {
      try {
        this.httpService.postApi(tenantData, 'tenant/create').subscribe((res) => {
          this.loaderService.hideLoader();
          if (res["success"]) {
            console.log(res['data']['_id'])
            //property to tenant form starts

            data['tenantId'] = res['data']['_id']
            data['propertyId'] = this.property,
            data['unitId'] = this.unit
            data['isAvailable'] = '1',          

            this.loaderService.showLoader('Adding Please wait ..').then(()=>{
              try{
                this.httpService.postApi(data, 'propertyToTenant/create').subscribe((res) => {
                  this.loaderService.hideLoader();
                  if(res["success"]){
                    this.alertService.presentAlert('Success','Successfully added','Okay');
                    this.stage = true;
                    //this.router.navigate(['/tenant-list'])
                    localStorage.setItem('assignPropertyToTenantData', JSON.stringify(res["data"]));
                  }else{
                    this.alertService.presentAlert('Error',res["error"]["message"],'Okay');
                  }    
                },(err)=>{

                  this.loaderService.hideLoader();
                  this.alertService.presentNetworkAlert();
                 });
              }catch(e){
                this.loaderService.hideLoader();
                this.alertService.presentAlert('Error','Something went wrong please try again','Okay');
              }
            })
            //property to tenant form ends 
            this.alertService.presentAlert('Success', 'Successfully added', 'Okay');
            //this.router.navigate(['/tenant-list'])
            localStorage.setItem('tenantData', JSON.stringify(res["data"]));
          } else {
            this.alertService.presentAlert('Error', res["message"], 'Okay');
          }
        }, (err) => {

          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
        });
      } catch (e) {
        this.loaderService.hideLoader();
        this.alertService.presentAlert('Error', 'Something went wrong please try again', 'Okay');
      }
    })
  }
  //tenant add ends here

  //agreement submit
  agreementSubmit(data){
    data['propertyId'] = this.property;
    console.log(data['propertyId']);
    this.loaderService.showLoader('Adding Please wait ..').then(()=>{
      try{
        this.httpService.postApi(data, 'agreements/create').subscribe((res) => {
          this.loaderService.hideLoader();
          if(res["success"]){
            this.alertService.presentAlert('Success','Successfully added','Okay');
            //this.router.navigate(['/agreement-list'])
          }else{
            this.alertService.presentAlert('Error',res["error"]["message"],'Okay');
          }    
        },(err)=>{
          
          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
         });
      }catch(e){
        this.loaderService.hideLoader();
        this.alertService.presentAlert('Error','Something went wrong please try again','Okay');
      }
    })
    
  }
  //agreement ends here
}

