
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';

@Component({
  selector: 'app-salesGroup-list',
  templateUrl: './sales-group-list.component.html',
  styleUrls: ['./sales-group-list.component.scss'],
})
export class SalesGroupListComponent implements OnInit {

 
  json: any=[];
  constructor(private router: Router, 
    public httpService: HttpServiceService, public alertService: AlertService,
    public util:UtilService,
    public loaderService: LoaderService) {

  }

  loadData(){
    this.loaderService.showLoader('Fetching Please wait ..').then(()=>{
      try{
        this.httpService.getApi('salesGroup/getAll').subscribe((res: any) => {
         // this.json = res.success?res.data:[];
         console.log(res)
          this.json = res.data;

          this.loaderService.hideLoader();
         },(err)=>{
          
          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
         });
      }catch(e){
        this.loaderService.hideLoader();
      }
   
    });
  }

  ngOnInit() {
    this.loadData();
  }
  add() {
    this.router.navigate(['/sales-group-add'])
  }

  details(data) {
    localStorage.setItem('salesGroupDetails', JSON.stringify(data));
    this.router.navigate(['/sales-group-details'])
  }
}


