
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss'],
})
export class UserListComponent implements OnInit {

  json: any=[];
  roles=[];
  constructor(private router: Router, 
    public httpService: HttpServiceService, public alertService: AlertService,
    public util:UtilService,
    public loaderService: LoaderService) {

  }

  loadData(){
    this.loaderService.showLoader('Fetching Please wait ..').then(()=>{
      try{
        this.httpService.getApi('role/getAll').subscribe((res) => {
          console.log("roles",res)
          this.roles = res['data']
        })
        this.httpService.getApi('user/getAll').subscribe((res: any) => {
          this.json = res.data;
          this.loaderService.hideLoader();
         },(err)=>{
          
          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
         });
      }catch(e){
        this.loaderService.hideLoader();
      }
   
    });
  }

  findRole(roleId){
    let tmp=(this.roles.find(item=>{return item._id==roleId}))
    return tmp.roleName;
  }

  ngOnInit() {
    this.loadData();
  }
  add() {
    this.router.navigate(['/user-add'])
  }

  details(data) {
    localStorage.setItem('userDetails', JSON.stringify(data));
    this.router.navigate(['/user-details'])
  }
}


