
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpServiceService } from 'src/app/http-service.service';
import { UtilService } from 'src/app/util.service';
import { LoaderService } from 'src/app/loader.service';
import { AlertService } from 'src/app/alert.service';

@Component({
  selector: 'app-units-add',
  templateUrl: './units-add.component.html',
  styleUrls: ['./units-add.component.scss'],
})
export class UnitsAddComponent implements OnInit {
  properties: any = [];
  amenities: any = [];
  facing: any = ["East", "West", "North", "South", "South-East", "South-West", "North-East", "North-West"];
  loading;
  types: any = ["1 BHK", "2 BHK", "3 BHK", "4 BHK"]
  inspection = false;
  units: any = [{
    unitDetails: '',
    propertyId: this.getpropertyId(),
    rent: '',
    area: '',
    type: '',
    additionalRooms: [],
    facing: '',
    amenities: [],
    inspection: [],
    propertyavailablefrom: '',
    isAvailable: ''
  }];
  dropdownList = [];
  dropdownSettings = {};
  property;
  constructor(private formBuilder: FormBuilder, private router: Router,
    public util: UtilService,
    public httpService: HttpServiceService, public alertService: AlertService,
    public loaderService: LoaderService) { }

  ngOnInit() {

    this.httpService.getApi('property/getAll').subscribe((res: any) => {
      console.log('properties', res['success'])
      this.properties = res.data
    })

    this.httpService.getApi('amenities/getAll').subscribe((res: any) => {
      console.log('amenities', res)
      this.amenities = res.data
      for (let i = 0; i < this.amenities.length; i++) {
        let json = {
          'name': this.amenities[i].name,
          '_id': this.amenities[i]._id
        }
        this.dropdownList.push(json)

      }
      console.log(this.dropdownList)
    })


    this.dropdownSettings = {
      singleSelection: false,
      idField: '_id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
  }
  onItemSelect(item: any) {
    console.log(item);
  }
  onSelectAll(items: any) {
    console.log(items);
  }

  inspectionChange() {
    this.inspection = true;
  }

changeProperty(){
  console.log(this.property)
  this.units.forEach(element => {
    element.propertyId = this.getpropertyId()
  });
}

  submit() {

    console.log("units json", this.units)
    this.loaderService.showLoader('Adding Please wait ..').then(() => {
      try {
        this.httpService.postApi(this.units, 'units/create').subscribe((res) => {
          this.loaderService.hideLoader();
          if (res["success"]) {
            this.alertService.presentAlert('Success', 'Successfully added', 'Okay');
            this.router.navigate(['/units-list'])
          } else {
            this.alertService.presentAlert('Error', res["message"], 'Okay');
          }
        }, (err) => {

          this.loaderService.hideLoader();
          this.alertService.presentNetworkAlert();
        });
      } catch (e) {
        this.loaderService.hideLoader();
        this.alertService.presentAlert('Error', 'Something went wrong please try again', 'Okay');
      }
    })

  }

  getpropertyId() {
    return this.property;
  }

  addRoom(data) {
    data.additionalRooms.push({
      name: '',
      quantity: ''
    });

    console.log(this.units)
  }
  addUnits() {
    this.units.push({
      unitDetails: '',
      propertyId: this.getpropertyId(),
      rent: '',
      area: '',
      type: '',
      additionalRooms: [],
      facing: '',
      amenities: '',
      inspection: '',
      propertyavailablefrom: '',
      isAvailable: ''
    });

    console.log(this.units)

  }

  deleteUnit(index) {
    if (this.units.length > 1) {
      this.units.splice(index, 1);
    }
  }
}


